<template>
  <div id="add-blog">
    <h2>添加博客</h2>
    <h2 v-if="blog.submited">添加成功</h2>
    <form action="" v-if="!blog.submited">
        <label for="">博客标题</label>
        <input type="text" v-model="blog.title" required>

        <label for="">博客内容</label>
        <textarea name="" id="" v-model="blog.content"></textarea>

        <div id="checkboxes">
            <label for="">Vue.js</label>
            <input type="checkbox" value="Vue.js" v-model="blog.categrories">
            <label for="">Node.js</label>
            <input type="checkbox" value="Node.js" v-model="blog.categrories">
            <label for="">Angular4</label>
            <input type="checkbox" value="Angular4" v-model="blog.categrories">
            <label for="">React.js</label>
            <input type="checkbox" value="React.js" v-model="blog.categrories">
        </div>

        <label for="">作者:</label>
        <select v-model="blog.author">
            <option v-for="author in blog.authors" :key="author">
                {{author}}
            </option>
        </select>
        <button @click.prevent="post">添加博客</button>
    </form>
    <hr>
    <div id="preview">
        <h3>博客总览</h3>
        <p>博客标题:{{blog.title}}</p>
        <p>博客内容:</p>
        <p>{{blog.content}}</p>
        <p>博客分类:</p>
        <ul>
            <li v-for="categrorie in blog.categrories" :key="categrorie">
                {{categrorie}}
            </li>
        </ul>
        <p>作者: {{blog.author}}</p>
    </div>

  </div>
</template>

<script>
export default {
  name: 'add-blog',
  data () {
    return {
        blog:{
            title:"",
            content:"",
            categrories:[],
            authors:["han","liu","li"],
            author:"",
            submited:false
        }
    }
  },
  methods:{
      post:function(){
          this.$axios.get("http://localhost:8084/test").then(result=>{
              alert(JSON.stringify(result.data))
              this.blog.submited=true
          })
      }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    #add-blog *{
        box-sizing: border-box;
    }
    #add-blog{
        margin: 20px auto;
        max-width: 600px;
        padding: 20px;
    }
    label{
        display: block;
        margin: 20px 0 10px;
    }
    input[type="text"],textarea,select{
        display: block;
        width: 100%;
        padding: 10px;
    }
    textarea{
        height: 200px;
    }
    #checkboxes label{
        display: inline-block;
        margin-top: 0;
    }
    #checkboxes input{
        display: inline-block;
        margin-right: 10px;
    }
    button{
        display: block;
        margin: 20px 0;
        background: crimson;
        color: #fff;
        border: 0;
        padding: 14px;
        border-radius: 4px;
        font-size: 18px;
        cursor: pointer
    }
    #preview{
        padding: 10px 20px;
        border:1px dotted #ccc;
        margin: 30px 0;
    }
    h3{
        margin-top: 10px;
    }

</style>



